/*---VARS---*/
$serif-font-size: 1.7rem;


/*---STYLE---*/
body{
  font-family: 'Source Sans Pro', sans-serif;
  padding: 0;
  margin: 0;
}

h2{
  margin-bottom: 10px;
}
.sidebar{
  background: #ededed;
}
#control{
  margin-top: 10px;
  margin-bottom: 20px;
  width: 260px;
}

.control-link{
  display: inline-block;
  margin: 3px;

}

#storypoints{
  margin-top: 10px;
  margin-bottom: 20px;
}

.storypoint{
  display: inline-block;
  margin: 3px;
  }

.active{
  background: red;
}

html, body, #map-container{
  height: 100%;
  width: 100%;
}
#map-container{
  z-index: 9;
  position: absolute;
}
.map-overlay{
  position: absolute;
  padding: 1rem;
  height: 90%;
  z-index:9;
  left: 4%
}
.sidebar{
  -webkit-box-shadow: 1px 1px 2px 0px rgba(50, 45, 50, 0.39);
  -moz-box-shadow:    1px 1px 2px 0px rgba(50, 45, 50, 0.39);
  box-shadow:         1px 1px 2px 0px rgba(50, 45, 50, 0.39);
}
.story-output{
  font-family: 'Source Serif Pro', serif;
  font-size: $serif-font-size;
  padding: 1.5rem;
  background: #fdfdfd;
  -webkit-box-shadow: 1px 1px 2px 0px rgba(50, 45, 50, 0.39);
  -moz-box-shadow:    1px 1px 2px 0px rgba(50, 45, 50, 0.39);
  box-shadow:         1px 1px 2px 0px rgba(50, 45, 50, 0.39);
}
